.button {
    transition: background-color 0.3s ease 0s;
    &:hover {
        background-color: $hover;
    }
}

.icon-download {
    transition: fill 0.3s ease 0s;
    &:hover {
        fill: $hover;
    }
}
.item-os {
    @include e(try) {
        transition: color 0.3s ease 0s;
        &:hover {
            color: rgba(0, 25, 74, 0.7);
        }
    }
}

.footer {
    @include e(link) {
        transition: 300ms;
        border-bottom: 1px solid transparent;
        &:hover {
            border-color: black;
        }
}}