.sharing {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding-bottom: 100px;
    padding-top: 100px;
    @include e(items) {
        gap: 30px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    }
    @include e(text) {
        margin-bottom: 30px;
    }

}

.item-sharing {
    padding: 30px 20px;
    border-radius: 10px;
    background: #FFF;
    box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.03);
    box-sizing: border-box;
    @include e(icon) {
        margin-bottom: 20px;
    }
    @include e(title) {
        margin-bottom: 15px;
        color: #00194A;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
    @include e(text) {
        font-size: 14px;
    }
}