.os {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(auto-fit, minmax(360px, 1fr));
    padding-top: 80px;
    padding-bottom: 80px;
    
}

.item-os {
    border-radius: 10px;
        background: #FFF;
        box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.03);
        padding: 30px;
    @include e(title) {
        color: $text;
        font-size: 22px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        margin-top: 20px;
        margin-bottom: 15px;
    }
    @include e(text) {
        font-size: 16px;
        font-style: normal;
        font-weight: 300;
        line-height: 26px;
        color: rgba(0, 25, 74, 0.8);
        margin-bottom: 21px;
    }
    @include e(links) {
        display: flex;
        justify-content: space-between;
        margin-bottom: 21px;
    }
    @include e(try) {
        color: rgba(0, 25, 74, 0.4991);
        font-family: Roboto;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        text-decoration-line: underline;
    }
    @include e(download) {
        background: none;
        border: none;
    }
}