.messaging {
    @include e(body) {
        // max-width: 466px;
    }
    @include e(title) {
        margin-top: 90px;
        margin-bottom: 30px;
    }
    @include e(text) {
        margin-bottom: 30px;
    }
}

.composition-messaging {
    // width: 50%;
    margin-bottom: 10%;
    & img {
        width: 100%;
        border-radius: 10px;
    }

    @include e(photo) {
        width: 90%;
        margin: auto;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: -15%;
            left: -60px;
            z-index: -1;
            background-image: url(../i/oval.svg);
            width: 251px;
            height: 251px;
        }

    }
    @include e(messages) {
        position: absolute;
        bottom: -50px;
        right: 15%;
    }
}

.message {
    background: $white;
    color: $text;
    display: flex;
    max-width: 390px;
    width: 100%;
    gap: 15px;
    padding: 10px;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    border-radius: 10px;
    font-weight: 300;
    line-height: normal;
    margin-bottom: 15px;
    @include m(2) {
        margin-left: 29px;

    }
    @include m(3) {
        margin-left: 58px;

    }

    @include e(author) {
        display: flex;
        justify-content: space-between;
    }

    @include e(name) {
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        margin-bottom: 5px;
    }

    @include e(num) {
        border-radius: 8px;
        background: $main;
        padding: 5px;
        color: $white;
        height: 100%;
    }

    @include e(messages) {
        display: flex;
        justify-content: space-between;
    }

    @include e(text) {
        max-width: 207px;
    }

    @include e(body) {
        width: 100%;
    }
}