.keep {
    padding-bottom: 110px;
    @include e(title) {
        margin-top: 60px;
        margin-bottom: 30px;
    }
}
.composition-keep {
    @include e(photo) {
        position: relative;
        display: flex;
        justify-content: center;
        width: 85%;
        margin: auto;
        margin-bottom: 5%;
        & > img {
            border-radius: 20px;
        width: 100%;

        }
        &::after {
            content: '';
            position: absolute;
            background: url(../i/oval.svg);
            width: 250px;
            height: 250px;
            top: -20%;
            right: -20px;
            z-index: -1;
        }
    }
    @include e(circle1) {
        position: absolute;
        width: 400px;
        height: 400px; 
        border: 58px solid #FFF8F9;
        border-radius: 50%;
        display: none;

        background: $white;
        justify-content: center;
        align-items: center;
        top: 230px;
        left: 135px;
    }
    
    @include e(circle2) {
        position: absolute;
        width: 140px;
        height: 140px;
        background: #FFF8F9;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.circle1 {
    box-sizing: border-box;
    @include e(photo1) {
        background: url('../i/sp1.jpg');
        background-size: 160px 160px;
        background-position: -30px -30px;
        z-index: 5;
        bottom: 0;
        right: 0;
        position: absolute;
        width: 95px;
        height: 95px;
        border-radius: 50%;
    }
    @include e(photo2) {
        background: url('../i/sp2.jpg');
        background-size: 70px 70px;
        background-position: 0 0;
        z-index: 5;
        position: absolute;
        width: 70px;
        height: 70px;
        border-radius: 50%;
        top: -25%;
        right: 40%;
    }
    @include e(photo3) {
        background: url('../i/sp3.jpg');
        background-size: 50px 50px;
        background-position: 0 0;
        z-index: 5;
        position: absolute;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        top: 30%;
        left: -5%;
    }
    @include e(photo4) {
        background: url('../i/sp4.jpg');
        background-size: 96px 96px;
        background-position: 72px 72px;
        z-index: 5;
        position: absolute;
        width: 46px;
        height: 46px;
        border-radius: 50%;
        top: 25%;
        right: -15%;
    }
    @include e(photo5) {
        background: url('../i/sp5.jpg');
        background-size: 90px 90px;
        background-position: 65px 65px;
        z-index: 5;
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        bottom: 0;
        left: 0;
    }
}
.circle2 {
    @include e(photo6) {
        background: url('../i/sp6.jpg');
        background-size: 54px 54px;
        background-position: 0 0;
        z-index: 5;
        top: -15%;
        right: 15%;
        position: absolute;
        width: 50px;
        height: 50px;
        border-radius: 50%;
    }
    
}