.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    @include e(logo) {
        color: $main;
        font-family: Roboto;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }
    @include e(button) {
        font-size: 14px;
        padding: 15px 20px;
        border-radius: 23px;

    }
}