.main-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 38px;
    padding-bottom: 50px;
    overflow: hidden;
    @include e(title) {
      color: $text;
      font-size: 60px;
      font-size: 40px;
      font-style: normal;
      font-weight: 500;
      margin-bottom: 20px;
      // line-height: 85px; /* 141.667% */
    }
    @include e(text) {
      margin-bottom: 70px;
      margin-bottom: 20px;
    }
}

