.composition {
  @include e(circle) {
     width: 594px;
     height: 594px;
     border: 1px solid rgba(255, 0, 67, 0.10);


      border-radius: 50%;
      // position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      &_2 {
        width: 454px;
        height: 454px;
        border: 1px solid rgba(255, 0, 67, 0.15);


        // &::before {
        //   content: '';
        //   position: absolute;
        //   background-image: url('../i/phone1.png');
        //   background-repeat: no-repeat;
        //   width: 100%;
        //   height: 100%;
        // }
        // &::after {
        //   content: '';
        //   position: absolute;
        //   background-image: url('../i/phone2.png');
        //   background-repeat: no-repeat;
        //   width: 100%;
        //   height: 100%;
        //   left: -30%;
        // }
      }
      &_3 {
        width: 312px;
        height: 312px;
        border: 1px solid rgba(255, 0, 67, 0.2);


      }
      
  }
  @include e(phone) {
    position: absolute;
    @include m(1) {
      left: -50%;
    }
    @include m(2) {
      left: 50%;
      top: 16%;
    }
  }
  @include e(photo) {
    border-radius: 50%;
    position: absolute;
    overflow: hidden;
    z-index: 5;
    @include m(1) {
      background-image: url('../i/p1.jpg');
      background-size: 147px 147px;
      background-position: 50% 50%;
      width: 90px;
      height: 90px;
      top: 10%;
      right: 20%;
    }
    @include m(2) {
      background-image: url('../i/p2.jpg');
      background-size: 120px 120px;
      background-position: 50% 50%;
      width: 70px;
      height: 70px;
      bottom: 0;
      left: 25%;
    }
    @include m(3) {
      background-image: url('../i/p3.jpg');
      background-size: 100px 100px;
      background-position: 50% 50%;
      width: 50px;
      height: 50px;
      top: 50%;
      left: -2%;
    }
    @include m(4) {
      background-image: url('../i/p4.jpg');
      background-size: 100px 100px;
      background-position: 50% 50%;
      width: 50px;
      height: 50px;
      top: -2%;
      left: 30%;
    }
    @include m(5) {
      background-image: url('../i/p5.jpg');
      background-size: 96px 96px;
      background-position: 50% 50%;
      width: 46px;
      height: 46px;
      top: -8%;
      right: 40%;
    }
    @include m(6) {
      background-image: url('../i/p6.jpg');
      background-size: 90px 90px;
      background-position: 50% 50%;
      width: 40px;
      height: 40px;
      bottom: 0;
      right: 30%;
    }
  }
}
 