.composition {
    @include e(photo) {
      @include m(1) {
        top: 15%;
        right: 8%;
      }
      @include m(2) {
        bottom: 5%;
        left: 15%;
      }
      @include m(3) {
        top: 50%;
        left: -2%;
      }
      @include m(4) {
        top: -5%;
        left: 45%;
      }
      @include m(5) {
        top: -5%;
        right: 30%;
      }
      @include m(6) {
        bottom: 0;
        right: 30%;
      }
    }
  }
   