html {
  font-size: $baseFontSize;
  color: $text;
}

body {
  font-family: $baseFontFamily;
}

h1 {
  font-weight: $mediumFontWeight;
}

.container {
  max-width: 1140px;
  margin: auto;
  padding: 10px;
}

.button {
  border-radius: 30px;
  background: $main;
  box-shadow: 0px 5px 50px 0px rgba(255, 0, 67, 0.10);
  border: none;
  color: #FFF;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  display: inline-flex;
  padding: 20px 60px;
}

.title {
  color: #00194A;
  font-family: Roboto;
  font-size: 35px;
  font-style: normal;
  font-weight: 500;
  line-height: 50px;
  margin-bottom: 30px;
}

.text {
  color: rgba(0, 25, 74, 0.8);
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: 28px; /* 155.556% */
}

.icon {
  fill: #FF0043;
  height: 60px;

}
.icon-download {
  width: 21px;
  height: 20px;
}