.sharing {
    justify-content: space-between;
    flex-direction: row;
    @include e(items) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
    }
    @include e(body) {
        max-width: 466px;
        margin-top: 115px;
    }
    
}

.item-sharing {
    max-width: 260px;
    @include m(span) {
        grid-row: span 2;
    }
}