.start {
    background: $text;
    border-radius: 25px;
    padding: 50px;
    text-align: center;
    @include e(title) {
        color: $white;
    }
    @include e(text) {
        color: $white;
        max-width: 580px;
        margin: 0 auto;
        margin-bottom: 50px;
    }
    @include e(button) {
        color: $white;
    }
}