.keep {
    display: flex;
    gap: 222px;
    justify-content: space-between;
    padding-bottom: 220px;

    @include e(title) {
        margin-top: 129px;
    }

    @include e(body) {
        max-width: 466px;
    }
}

.composition-keep {
    @include e(circle1) {
        border-radius: 50%;
        display: flex;
        top: 230px;
        left: 135px;
    }

    @include e(photo) {
        &::after {
            right: -90px;
        }
    }
}